body {
  margin: 0;
  font-family: "Poppins", sans-serif;
}

.header {
  background-image: url("/assets/images/header.jpg");

  // mobile
  @media (max-width: 768px) {
    height: 330px;
    background-size: cover;
    background-position: center;
  }
  // DESKTOP
  @media (min-width: 768px) {
    height: 100vh;
    background-size: cover;
  }
}

.hamburger {
  // mobile
  @media (max-width: 768px) {
    width: 18px;
    height: 12px;
  }
  // DESKTOP
  @media (min-width: 768px) {
    display: none;
  }
}

.logo {
}
.links {
  display: none;
  //tablet
  @media (min-width: 768px) {
    display: block;
  }
}

.container {
